.statistics {
    display: inline-block;
    flex-direction: column;
    box-shadow: 2px 2px 0px 2px rgba(0, 0, 0, 0.02);
    font-family: 'Poppins', sans-serif;
    margin-bottom: 20px;
    margin-right: 50px;
  }
  
  .title {
    margin: 0;
    padding: 30px 0;
    text-align: center;
    text-transform: uppercase;
    font-size: 16px;
    color: #475965;
    font-weight: 600;
    width: 100%;
  }
  
  .stat_list {
    margin: 0;
    padding: 0;
    display: flex;
    list-style: none;
    justify-content: stretch;
  }
  
  .item {
    height: 60px;
    width: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .label {
    display: block;
    font-size: 12px;
    font-weight: 300;
    color: #fff;
  }
  
  .percentage {
    display: block;
    font-size: 18px;
    font-weight: 400;
    color: #fff;
  }